@import "../fonts/fonts.css";

@import "tailwindcss" source("../../src");
@plugin "@tailwindcss/typography";

@theme {
  --font-sora: "Sora", var(--font-serif);
  --font-manrope: "Manrope", var(--font-default);
}

body {
  @apply font-sora;
}

/* Viewport container styles */
.viewport-container {
  @apply relative w-full h-screen overflow-auto cursor-grab;
  
  /* Hide scrollbars but keep functionality */
  scrollbar-width: none;
  -ms-overflow-style: none;
  
  /* Performance optimizations */
  -webkit-overflow-scrolling: touch;
  overscroll-behavior: contain;
  
  &::-webkit-scrollbar {
    display: none;
  }
  
  /* Disable user selection during drag */
  &.is-dragging {
    @apply cursor-grabbing;
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
  }
}

/* Viewport canvas - the large scrollable area */
.viewport-canvas {
  @apply relative;
  min-width: 4964px;
  min-height: 2690px;
  
  /* Prevent image drag ghost */
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
  user-drag: none;
}

/* SVG background */
.viewport-background {
  @apply absolute top-0 left-0 w-full h-full;
  z-index: 0;
}

/* Box styles - matching SVG shadow specs */
.viewport-box {
  @apply absolute bg-white overflow-hidden transition-all duration-300 ease-out;
  z-index: 1;
  border-radius: 10px; /* Matching SVG rx="10" */
  background-color: #D8D8D8; /* Matching SVG fill */
  
  /* Shadow matching SVG filter: offset dx="5" dy="4", blur="7.5", opacity="0.254807692" */
  box-shadow: 5px 4px 15px rgba(0, 0, 0, 0.255);
  
  &:hover {
    transform: scale(1.02);
    box-shadow: 5px 4px 20px rgba(0, 0, 0, 0.35);
  }
}

/* Box content styles */
.box-content {
  @apply w-full h-full;
  
  h3 {
    @apply text-xl font-bold mb-4 text-gray-800;
  }
  
  p {
    @apply text-gray-600;
  }
}

/* Photo placeholder */
.photo-placeholder {
  @apply w-full h-full flex items-center justify-center bg-gray-100 rounded;
  
  h3 {
    @apply text-center text-gray-500;
  }
}

/* Text content */
.text-content {
  @apply prose prose-sm max-w-none;
}

/* Mixed content */
.mixed-content {
  @apply flex flex-col h-full;
  
  .photo-area {
    @apply flex-1 bg-gray-100 rounded mb-4;
    min-height: 200px;
  }
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .viewport-box {
    /* Keep boxes readable on mobile */
    min-width: 200px;
    min-height: 150px;
  }
}
